import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { TranslateModule, TranslateLoader, TranslateService } from '@ngx-translate/core';
import { Config } from './../_helpers/config';



@Component({
  moduleId: module.id,
  templateUrl: 'international.component.html',
  providers: []
})

export class InternationalComponent implements OnInit {
  countries: any[];
  constructor(private router: Router, public translate: TranslateService) { }

  ngOnInit() {
    this.countries = Config.BES_COUNTRIES;


  }

  chooseCountry(code: string, name: string) {
    var currentUser: any = JSON.parse(localStorage.getItem('currentUser'));
    var customer = currentUser != null ? (currentUser.hasOwnProperty('customer') ? currentUser.customer : null)  : null;
    currentUser = {
      country: {
        key: code,
        name: name
      },
      username: currentUser != null ? currentUser.username : null,
      token: currentUser != null ? currentUser.token : null,
      customer: customer
    };
    localStorage.setItem('currentUser', JSON.stringify(currentUser));
    this.ngLanguageInit(code);
    this.router.navigate(['/index']);
  }
  ngLanguageInit(lang: string) {
    let browserLang = this.translate.getBrowserLang();
    browserLang = browserLang.match(/en|es|it|nl|de|be|fr/) ? browserLang : 'fr';
    lang = lang != null ? lang : browserLang;
    this.translate.use(lang);
  }
}


